import { useLocation } from "react-router-dom";
import style from "./StudentAdmissionFees.module.css";
import { useStudentAdmissionFeeList } from "../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import Loading from "../../Loader/Loading";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
import StudentAdmissionFeesCard from "./StudentAdmissionFeesCard";
import StudentAdmissionSelfFeesCard from "./PayFees/StudentAdmissionSelfFeesCard";
import { Fragment } from "react";
import StudentHostelFeesCard from "./StudentHostelFees/StudentHostelFeesCard";
import StudentHostelSelfFeesCard from "./PayFees/StudentHostelSelfFeesCard";

const StudentAdmissionFees = ({ isEditable }) => {
  const getQuery = useLocation();
  const { studentAdmissionFeeList, studentAdmissionFeeListLoading } =
    useStudentAdmissionFeeList({
      sid: getQuery.state?.sid,
      skip: !getQuery.state?.sid,
    });

  // console.info("getQuery", studentAdmissionFeeList);
  // console.info("sdbhfsdsd", getQuery.state);
  return (
    <>
      <UserMemberContentWrapper>
        <section className={style.af_container}>
          {getQuery.state?.viewAs === "STUDENT"
            ? studentAdmissionFeeList?.map((admission, index) => (
                <Fragment key={admission?._id}>
                  {admission?.remaining_flow === "Hostel Application" ? (
                    <StudentHostelSelfFeesCard
                      admission={admission}
                      howManyCard={getQuery.state?.howManyCard}
                      isEditable={isEditable}
                      parentIndex={index}
                    />
                  ) : (
                    <StudentAdmissionSelfFeesCard
                      admission={admission}
                      howManyCard={getQuery.state?.howManyCard}
                      isEditable={isEditable}
                      parentIndex={index}
                    />
                  )}
                </Fragment>
              ))
            : studentAdmissionFeeList?.map((admission, index) => (
                <Fragment key={admission?._id}>
                  {admission?.remaining_flow === "Hostel Application" ? (
                    <StudentHostelFeesCard
                      admission={admission}
                      parentIndex={index}
                    />
                  ) : (
                    <StudentAdmissionFeesCard
                      admission={admission}
                      parentIndex={index}
                    />
                  )}
                </Fragment>
              ))}

          {studentAdmissionFeeListLoading ? (
            <Loading />
          ) : (
            studentAdmissionFeeList?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission fees list."
              />
            )
          )}
        </section>
      </UserMemberContentWrapper>
    </>
  );
};

export default StudentAdmissionFees;
